.container{
  justify-self: center;
  align-items: center;
  text-align: center;
  display: flex;
   flex-direction: column;

}

.count{
  padding: 5px;
  font-size: 24px;
  color: white;
  background: linear-gradient(20deg, rgb(25, 179, 240),rgb(0, 255, 34));
  border: 1px solid wheat;
  border-radius: 20px;
  display: flex;
  margin: auto;
  align-items: center;
  text-align: center;
  order: -2;
}
.count:hover{
  background: linear-gradient(-30deg, rgb(243, 38, 11), rgb(225, 0, 255));
}
h2{
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
  font-size: 50px;
  font-family: 'Courier New', Courier, monospace;
  background: linear-gradient(50deg, rgb(238, 37, 178),rgb(54, 200, 226));
  width: 25%;
  margin: auto;
  margin-top: 10px;
  border-radius:20px ;
  font-weight: 600;
  color: black;
}
h2:hover{
  color: aliceblue;
}App {
  text-align: center;
}

